<template>
  <div class="charts-container">
    <div ref="barChart" class="chart"></div>
    <div ref="lineChart" class="chart"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  name: 'TaskStats',
  props: {
    todoItems: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      barChart: null,
      lineChart: null
    };
  },
  watch: {
    // 监听 todoItems 的变化
    todoItems: {
      handler() {
        this.updateCharts();
      },
      deep: true // 深度监听数组内部的变化
    }
  },
  mounted() {
    this.initCharts();
    window.addEventListener('resize', this.handleResize);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
    if (this.barChart) {
      this.barChart.dispose();
    }
    if (this.lineChart) {
      this.lineChart.dispose();
    }
  },
  methods: {
    initCharts() {
      this.barChart = echarts.init(this.$refs.barChart);
      this.lineChart = echarts.init(this.$refs.lineChart);
      this.updateCharts();
    },
    updateCharts() {
      if (!this.barChart || !this.lineChart) return;

      // 更新柱状图
      const completed = this.todoItems.filter(item => item.done).length;
      const pending = this.todoItems.length - completed;

      const barOption = {
        title: {
          text: '任务完成情况'
        },
        tooltip: {},
        xAxis: {
          data: ['已完成', '待完成']
        },
        yAxis: {
          minInterval: 1 // 确保y轴刻度为整数
        },
        series: [{
          name: '任务数量',
          type: 'bar',
          data: [completed, pending],
          itemStyle: {
            color: function(params) {
              return params.dataIndex === 0 ? '#91cc75' : '#fac858';
            }
          }
        }]
      };
      
      // 更新折线图
      const dates = Array.from({length: 7}, (_, i) => {
        const date = new Date();
        date.setDate(date.getDate() - (6 - i));
        return date.toLocaleDateString();
      });

      // 这里使用实际的完成任务数据
      // 注意：这是示例数据，实际应用中你可能需要从后端获取每天的完成数据
      const completedTasks = Array(7).fill(completed);

      const lineOption = {
        title: {
          text: '任务完成趋势'
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: dates,
          axisLabel: {
            rotate: 45
          }
        },
        yAxis: {
          type: 'value',
          name: '完成任务数',
          minInterval: 1
        },
        series: [{
          data: completedTasks,
          type: 'line',
          smooth: true,
          lineStyle: {
            color: '#5470c6'
          },
          itemStyle: {
            color: '#5470c6'
          }
        }]
      };

      this.barChart.setOption(barOption);
      this.lineChart.setOption(lineOption);
    },

    handleResize() {
      this.barChart && this.barChart.resize();
      this.lineChart && this.lineChart.resize();
    }
  }
};
</script>

<style scoped>
.charts-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
}

.chart {
  width: 100%;
  height: 400px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.1);
}

@media (min-width: 1200px) {
  .chart {
    width: calc(50% - 10px);
  }
}
</style> 