<template>
    <form class="stack-small" @submit.prevent="onSubmit">
      <div>
        <label class="edit-label">Edit Name for &quot;{{label}}&quot;</label>
        <input
          :id="id"
          ref="labelInput"
          type="text"
          autocomplete="off"
          v-model.lazy.trim="newLabel" />
      </div>
      <div class="btn-group">
        <el-button 
          type="warning"
          :style="buttonStyle"
          size="large" 
          class="custom-button" 
          @click="onCancel" 
          round>
          <el-icon class="button-icon"><Close /></el-icon>
          Cancel
        </el-button>
        <el-button 
          type="success" 
          :style="buttonStyle"
          size="large" 
          class="custom-button" 
          @click="onSubmit" 
          round>
          <el-icon class="button-icon"><Check /></el-icon>
          Save
        </el-button>
      </div>
    </form>
  </template>
  <script>
  import { ElMessage, ElMessageBox } from 'element-plus'
  import { Check, Close } from '@element-plus/icons-vue'
    export default {
      components: {
        Check,
        Close
      },
      props: {
        label: {
          type: String,
          required: true,
        },
        id: {
          type: String,
          required: true,
        },
      },
      data() {
        return {
          newLabel: this.label,
          buttonStyle: {
            fontSize: '16px',
            padding: '12px 24px',
            margin: '0 10px'
          }
        };
      },
      methods: {
        onSubmit() {
          if (this.newLabel && this.newLabel !== this.label) {
            this.$emit("item-edited", this.newLabel);
            ElMessage({
              type: 'success',
              message: 'Successfully updated',
            });
          }
        },
        
        onCancel() {
      ElMessageBox.confirm(
        'Are you sure you want to cancel editing? All changes will be lost.',
        'Warning',
        {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning',
          draggable: true,
        }
      ).then(() => {
        this.$emit("edit-cancelled");
        ElMessage({
          type: 'info',
          message: 'Edit canceled',
        });
      }).catch(() => {
        ElMessage({
          type: 'info',
          message: 'Edit not canceled',
        });
      });
    },
  },
      mounted() {
   const labelInputRef = this.$refs.labelInput;
   labelInputRef.focus();
},
    };
  </script>
  <style scoped>
    .btn-group {
      display: flex;
      justify-content: center;
      gap: 20px;
      margin-top: 20px;
    }
    .custom-button {
      transition: all 0.3s ease;
      display: flex;
      align-items: center;
      justify-content: center;
      min-width: 120px;
    }
    .custom-button:hover {
      transform: translateY(-2px);
      box-shadow: 0 5px 15px rgba(0,0,0,0.1);
    }
    .button-icon {
      margin-right: 8px;
    }
    .edit-label {
      font-size: 18px;
      color: #606266;
      margin-bottom: 12px;
      display: block;
    }
    input {
      width: 100%;
      padding: 12px;
      border: 2px solid #dcdfe6;
      border-radius: 8px;
      font-size: 16px;
      transition: all 0.3s ease;
    }
    input:focus {
      border-color: #409eff;
      outline: none;
      box-shadow: 0 0 0 2px rgba(64,158,255,0.2);
    }
    form {
      background: white;
      padding: 24px;
      border-radius: 12px;
      box-shadow: 0 2px 12px 0 rgba(0,0,0,0.1);
    }
  </style>
  