<template>
  <div class="stack-small" v-if="!isEditing">
    <div class="custom-checkbox">
      <input
        type="checkbox"
        class="checkbox"
        :id="id"
        :checked="isDone"
        @change="$emit('checkbox-changed')" />
      <label :for="id" class="checkbox-label">{{label}}</label>
    </div>
    <div class="btn-group">
      <el-button 
        type="primary" 
        :style="buttonStyle"
        size="large" 
        class="custom-button" 
        ref="editButton" 
        @click="toggleToItemEditForm" 
        round>
        <el-icon class="button-icon"><Edit /></el-icon>
        Edit <span class="visually-hidden">{{label}}</span>
      </el-button>
      <el-button 
        type="danger" 
        :style="buttonStyle"
        size="large" 
        class="custom-button" 
        @click="deleteToDo" 
        round>
        <el-icon class="button-icon"><Delete /></el-icon>
        Delete <span class="visually-hidden">{{label}}</span>
      </el-button>
    </div>
  </div>
  <to-do-item-edit-form v-else :id="id" :label="label" @item-edited="itemEdited" @edit-cancelled="editCancelled"></to-do-item-edit-form>
</template>

<script>
import ToDoItemEditForm from "./ToDoItemEditForm";
import { Edit, Delete } from '@element-plus/icons-vue';
import { ElMessage, ElMessageBox } from 'element-plus';

export default {
  components: {
    ToDoItemEditForm,
    Edit,
    Delete
  },
  props: {
    label: { required: true, type: String },
    done: { default: false, type: Boolean },
    id: { required: true, type: String }
  },
  data() {
    return {
      isEditing: false,
      buttonStyle: {
        fontSize: '16px',
        padding: '12px 24px',
        margin: '0 10px'
      }
    };
  },
  computed: {
    isDone() {
      return this.done;
    }
  },
  methods: {
    deleteToDo() {
      ElMessageBox.confirm(
        'Are you sure you want to delete this task?',
        'Warning',
        {
          confirmButtonText: 'Delete',
          cancelButtonText: 'Cancel',
          type: 'warning',
          draggable: true,
        }
      )
      .then(() => {
        this.$emit('item-deleted');
        ElMessage({
          type: 'success',
          message: 'Task deleted successfully',
        });
      })
      .catch(() => {
        ElMessage({
          type: 'info',
          message: 'Delete canceled',
        });
      });
    },
    toggleToItemEditForm() {
      this.isEditing = true;
    },
    itemEdited(newItemName) {
      this.$emit("item-edited", newItemName);
      this.isEditing = false;
      this.focusOnEditButton();
    },
    editCancelled() {
      this.isEditing = false;
      this.focusOnEditButton();
    },
    focusOnEditButton() {
      this.$nextTick(() => {
        const editButtonRef = this.$refs.editButton;
        if (editButtonRef && editButtonRef.$el) {
          editButtonRef.$el.focus();
        }
      });
    }
  }
};
</script>

<style scoped>
.btn-group {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.custom-button {
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 120px;
}

.custom-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.button-icon {
  margin-right: 8px;
}

.custom-checkbox {
  margin-bottom: 20px;
  padding: 12px;
  background: #f5f7fa;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.custom-checkbox:hover {
  background: #ecf5ff;
}

.checkbox {
  margin-right: 12px;
  transform: scale(1.2);
  cursor: pointer;
}

.checkbox-label {
  font-size: 16px;
  color: #606266;
  cursor: pointer;
  user-select: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  white-space: nowrap;
  width: 1px;
}

/* 完成状态的样式 */
.custom-checkbox input:checked + .checkbox-label {
  text-decoration: line-through;
  color: #909399;
}
</style>